import * as R from 'ramda'
import React, { Fragment } from 'react'
import styled from 'react-emotion'
import ScaleLoader from 'react-spinners/ScaleLoader'
import Component from '@reach/component-component'
import { Dialog, DialogOverlay, DialogContent } from '@reach/dialog'

const MARGIN = '50px'

const Loading = styled('div')`
  margin-top: ${MARGIN};
  text-align: center;
  font-size: 30px;
`

const Wrapper = styled('div')`
  margin-top: ${MARGIN};
`

const ItemWrapper = styled('div')`
  display: flex;
  align-items: center;
  width: 100%;
  min-width: 320px;
  border: 1px dashed white;
  border-radius: 5px;
`

const Name = styled('div')`
  flex: 1;
  padding: 20px;
`

const Version = styled('span')`
  opacity: 0.5;
`

const License = styled('div')`
  border-left: 1px dashed white;
  padding: 20px;
`

const getLicensesName = R.pipe(
  R.path(['licenseSources', 'package', 'sources']),
  R.defaultTo([]),
  R.map(R.prop('license')),
)

const Item = item => {
  const name = R.prop('name', item)
  const version = R.prop('version', item)
  const licenses = getLicensesName(item)

  return (
    <ItemWrapper>
      <Name>
        {name}
        <Version>@{version}</Version>
      </Name>
      <Component initialState={{ showDialog: false }}>
        {({ state, setState }) => (
          <Fragment>
            <License>{licenses.join(', ')}</License>
            <Dialog isOpen={state.showDialog}>
              <button
                className="close-button"
                onClick={() => setState({ showDialog: false })}
              >
                <span aria-hidden>Ã—</span>
              </button>
              <p>Hello there. I am a dialog</p>
            </Dialog>
          </Fragment>
        )}
      </Component>
    </ItemWrapper>
  )
}

export const Licenses = ({ isLoading, error, data }) => {
  if (isLoading) {
    return (
      <Loading>
        <ScaleLoader size={120} color="white" loading />
      </Loading>
    )
  }

  return (
    <Wrapper>
      <Item item={{ name: 'react', version: '1' }} />
      {data && data.length > 0 && data.map(item => <Item item={item} />)}
    </Wrapper>
  )
}



